import React, { Component } from "react";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { connect } from "react-redux";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class AccordCiok extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  setCiok() {
    this.props.change("ciokBrutto", this.props.totalBrutoCiok);
    this.props.change("ciokNetto", parseFloat(this.props.totalCiok).toFixed(3));
  }
  render() {
    return (
      <div className="col-lg-6">
        <div id="accordion" className="custom-accordion mb-4">
          <div className="card mb-0 bg-dark">
            <div className="card-header" id="headingOne">
              <div className="row">
                <div className="col-lg-8 col-md-8">
                  <h5 className="m-0">
                    <a
                      className="custom-accordion-title d-block pt-2 pb-2 text-white"
                      data-toggle="collapse"
                      href="#collapseCiok"
                      aria-expanded="true"
                      aria-controls="collapseCiok"
                      onClick={() => this.setCiok()}
                    >
                      Ciok
                      <span className="float-right">
                        <i className="mdi mdi-chevron-down accordion-arrow" />
                      </span>
                    </a>
                  </h5>
                </div>
                <div className="col-md-4 col-lg-4">
                  <label htmlFor="" style={{ color: "white" }}>
                    Netto
                  </label>
                  <Field
                    name="ciokNetto"
                    type="number"
                    component="input"
                    className="form-control"
                    readOnly={true}
                  />
                </div>
              </div>
            </div>
            <div
              id="collapseCiok"
              className="collapse"
              aria-labelledby="headingOne"
              data-parent="#accordion"
            >
              <div className="card-body bg-white">
                <div className="row">
                  <div className="col-lg-2">
                    <label htmlFor="">.</label>
                    <button
                      className="btn btn-primary btn-block"
                      type="button"
                      data-target="#ciokModal"
                      data-toggle="modal"
                    >
                      +
                    </button>
                  </div>
                  {this.props.totalCiok ? (
                    <>
                      <div className="col-lg-5 col-md-5">
                        <Field
                          name="ciokBrutto"
                          label="Bruto"
                          type="number"
                          component={renderField}
                          readOnly={true}
                          onBlur={this.setCiok()}
                        />
                      </div>
                      <div className="col-lg-5 col-md-5">
                        <Field
                          name="ciokNetto"
                          label="Netto"
                          type="number"
                          component={renderField}
                          readOnly={true}
                          onBlur={this.setCiok()}
                        />
                      </div>
                    </>
                  ) : (
                    <>
                      <div className="col-lg-5 col-md-5">
                        <Field
                          name="ciokBrutto"
                          label="Bruto"
                          type="number"
                          component={renderField}
                          readOnly={true}
                          onBlur={this.setCiok()}
                        />
                      </div>
                      <div className="col-lg-5 col-md-5">
                        <Field
                          name="ciokNetto"
                          label="Netto"
                          type="number"
                          component={renderField}
                          readOnly={true}
                          onBlur={this.setCiok()}
                        />
                      </div>
                    </>
                  )}
                </div>
              </div>
            </div>
          </div>
          {/* end card*/}
        </div>
      </div>
    );
  }
}
AccordCiok = reduxForm({
  form: "AccordCiok",
  enableReinitialize: true,
})(AccordCiok);
const selector = formValueSelector("AccordCiok"); // <-- same as form name
export default connect((state) => {
  const { harga, cash } = selector(state, "harga", "cash");
  return {
    cashNetto: (parseFloat(cash) / parseFloat(harga)).toFixed(3),
  };
})(AccordCiok);
